.auth{
    display:flex;
    flex-direction:column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-color: #b9e7e7;
}

.auth h1{
    font-size: 20px;
    color: teal;
    margin-bottom: 20px;
} 

.form{
    display:flex;
    flex-direction: column;
    padding:50px;
    background-color: white;
    width: 200px;
        grid-gap: 20px;
        gap: 20px;

   
}

.form input{
    padding:10px;
    border:none;
    border-bottom: 1px solid grey;
}
.form button{
    padding: 10px;
    border: none;
    background-color: teal;
    cursor: pointer;
    color: white ;
}
.form span{
    font-size: 12px;
    text-align: center  ;
}

.form p{
    font-size: 12px;
    color: red;
    text-align: center;
}

/*HOme ***********************************************************************/
.Home{
    .posts{
        margin-top:50px;
        display: flex;
        flex-direction: column;
        grid-gap: 150px;
        gap: 150px;

        .post{
            display: flex;
            grid-gap: 100px;
            gap: 100px;
            &:nth-child(2n+1){
                flex-direction: row-reverse;
            }
            .img{
                flex:2 1;
                position: relative;

                &::after{
                    content:"";
                    width: 100%;
                    height: 100%;
                    background-color:#b9e7e7;
                    position:absolute;
                    top: 20px;
                    left: -20px;
                    z-index: -1;
                }
            }
            .img img{
                width:100%;
                max-height:  400px;
                object-fit: cover;
            }
            .content {
                    flex:3 1;

                    
                .link{
                    text-decoration: none;
                    color: black;
                    font-size: 20px;
                    display: flex;
                    flex-direction: column;
                    grid-gap:40px;
                    gap:40px;
                    justify-content: space-between;

                }
                .first-two-lines {
                    overflow: hidden;
                    display: -webkit-box;
                    -webkit-line-clamp: 2; /* Number of lines to display */
                    -webkit-box-orient: vertical;
                  }
                  

            }
            .content h1{
                font-size: 48px;
            }
            .content p{
                font-size: 18px;
            }
            .content button{
                width: -webkit-max-content;
                width: max-content;
                padding: 10px 20px;
                border: none;
                cursor: pointer;
                background-color: white;
                border: 1px solid teal;
                color: teal;

                &:hover{
                    border: 1px solid white;
                    background-color:#b9e7e7;
                    color: black;

                }

            }
            
        }
    }
}

/* Single ***********************************************************/
.single{
    display: flex;
    margin-top: 40px;
    grid-gap: 40px;
    gap: 40px;
    .content p{
        text-align: justify;
    }
   
    .content{
        flex: 5 1;
        display: flex;
        flex-direction: column;
        /* gap: 30px; */

        .user {
            display: flex;
            align-items: center;
            font-size: 14px;

            .info{
                margin: 0;
                margin-top: 10px;
                padding: 15px;
                .nm{
                    margin-top: 0;
                }
            }
            .info span{
                font-weight: bold;
            }

            .edit{
                display: flex;
                grid-gap: 5px;
                gap: 5px;
            }
            .edit img{
                width: 20px;
                height: 20px;
                cursor: pointer;
            }
          

        }

        .user img{
            width: 50px;
            height: 50px;
            border-radius: 50%;
            object-fit: cover;
        }
        
        

       
        

    }
    .content img{
            width: 100%;
            height: 300px;
            object-fit: cover;


    }
    .menu{
        flex: 2 1;
    }
}

/*Write *************************************************************************************/

.add{
    margin-top: 20px;
    display: flex;
    grid-gap: 30px;
    gap: 30px;

    .content{
        flex: 5 1;
        display: flex;
        flex-direction: column;
        grid-gap: 20px;
        gap: 20px;
    }
    .content input{
        padding: 10px;
        border: 1px solid lightgray;

    }

    .editorContainer{
        height: 300px;
        overflow: scroll;
        border: 1px solid lightgray;

        .editor{
            height: 100%;
            border: none;
        }
    }



    .menuwrite{
        flex:2 1;
        display: flex; 
        flex-direction: column; 
        /* gap: 20px;  */


        .item{
            border: 1px solid lightgray;
            padding: 10px ;
            flex: 1 1;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            font-size: 12px;
            color: #555;
            /* align-items:flex-start ; */

            .file{
                text-decoration: underline;
                cursor:pointer
            }

            .buttons{
                display: flex;
                justify-content: space-between;

                :first-child{
                    cursor: pointer;
                    color: teal;
                    background-color: white;
                    border: 1px solid teal;
                    padding: 3px 5px;
                }
                :last-child{
                    cursor: pointer;
                    color: white;
                    background-color: teal;
                    border: 1px solid teal;
                    padding: 3px 5px;
                }
            }

            .cat{
                display: flex;
                align-items: center;
                grid-gap: 2px;
                gap: 2px;
                color: teal;

            }

        }
       
        .item h1{
            font-size: 20px;
            margin: 0;

        }

        .item label{
            display: flex;
           
        }
       
        
    }

}


